.main-navigation {
  overflow: hidden;

  left: 0;
  top: 0;
  width: 100%;
  height: 3.5rem;
  background: #c44536;
  padding: 0 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;
  overflow: hidden;
}

.main-navigation-logo h1 {
  margin: 0;
  font-family: "Open Sans", sans-serif;
  font-size: 1.5rem;
  color: #fff;
}

.main-navigation-item {
  margin-left: 1.5rem;
}

.main-navigation-item ul {
  font-family: "Roboto", sans-serif;
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
  align-items: center;
}

.main-navigation-item li {
  margin: 0 1rem;
}

.main-navigation-item a,
.main-navigation-item button {
  text-decoration: none;
  color: white;
  padding: 0.25rem 0.5rem;
  border: none;
  font: inherit;
  background: transparent;
  cursor: pointer;
  vertical-align: middle;
  margin: 0;
}

.main-navigation-item a:hover,
.main-navigation-item button:hover {
  background: #ffffff;
  color: #c44536;
  border-radius: 5px;
}
